<template>
    <input :value="value" @input="onInput" :placeholder="placeholder" :ref="name" :class="`${className}${config.inline ? ' hidden' : ''}`">
</template>

<script>
import 'flatpickr/dist/flatpickr.css';
import flatpickr from 'flatpickr';

export default {
    props: {
        placeholder: {
            type: String,
            required: false,
            default: 'Select Date'
        },
        className: {
            type: String,
            required: false,
            default: `live-form-control date-picker`
        },
        value: {
            default: null,
            required: false,
        },
        name: {
            type: String,
            required: true,
        },
        config: {
            type: Object,
            default: () => ({
                inline: true,
            })
        },
        /**
         * returns
         *
         * selectedDates - an array of Date objects selected by user
         * dateStr - a string representation of the latest selected Date object by the user
         * instance - the flatpickr object, containing various methods and properties
         */
        onChange: Function
    },
    mounted() {
        const instance = flatpickr(this.$refs[this.name], this.config);

        instance.config.onChange.push(this.onChange)
    },
    methods: {
        /**
         * Watch for value changed by date picker itself and notify parent component
         *
         * @param {Event} e
         * @return {void}
         */
        onInput(e) {
            this.$nextTick(() => {
                this.$emit('input', e.target.value);
            })
        }
    }
}
</script>